/*=====================
    1.1.Button CSS start
    ==========================*/
button {
  cursor: pointer;
}

.btn {
  line-height: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  border-radius: 0;
  transition: 0.3s ease-in-out;
}
.btn:hover {
  transition: 0.3s ease-in-out;
}
.btn:focus {
  box-shadow: none;
}

.btn-solid {
  padding: 13px 29px;
  color: #ffffff !important;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult) !important;
  background-image: -webkit-linear-gradient(30deg, var(--theme-deafult) 50%, transparent 50%) !important;
  background-image: linear-gradient(30deg, var(--theme-deafult) 50%, transparent 50%) !important;
  background-size: 850px;
  background-repeat: no-repeat;
  background-position: 0;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
}
.btn-solid:hover {
  background-position: 100%;
  color: #000000 !important;
  background-color: #ffffff;
}
.btn-solid.black-btn {
  background-image: -webkit-linear-gradient(30deg, #222222 50%, transparent 50%);
  background-image: linear-gradient(30deg, #222222 50%, transparent 50%);
  border: 2px solid #222222;
}
.btn-solid:focus {
  color: #ffffff;
}
.btn-solid.btn-gradient {
  background: var(--theme-deafult);
  background: linear-gradient(180deg, var(--theme-gradient1) 0%, var(--theme-gradient2) 99%);
  transition: background 300ms ease-in-out;
  background-size: 300% 100%;
  border: none;
}
.btn-solid.btn-gradient:hover {
  background: linear-gradient(-180deg, var(--theme-gradient2) 0%, var(--theme-gradient1) 99%);
  transition: background 300ms ease-in-out;
  color: white;
}
.btn-solid.btn-green {
  background-image: linear-gradient(to right, var(--theme-gradient1), var(--theme-gradient2));
  border: none;
  background-color: var(--theme-deafult);
}
.btn-solid.btn-green:hover {
  background-color: var(--theme-deafult);
  background-image: none;
  color: white;
}
.btn-solid.btn-sm, .btn-group-sm > .btn-solid.btn {
  padding: 9px 16px;
}
.btn-solid.btn-xs {
  padding: 5px 8px;
  text-transform: capitalize;
}

.btn-outline {
  display: inline-block;
  padding: 13px 29px;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult);
  position: relative;
  color: #000000;
}
.btn-outline:before {
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--theme-deafult);
  z-index: -2;
}
.btn-outline:hover, .btn-outline:focus {
  color: #ffffff !important;
  background-color: var(--theme-deafult);
}
.btn-outline:hover i, .btn-outline:focus i {
  color: #ffffff !important;
}
.btn-outline:hover:before, .btn-outline:focus:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}
.btn-outline.btn-sm, .btn-group-sm > .btn-outline.btn {
  padding: 9px 16px;
  font-size: 12px;
}

button.btn.btn-solid:active, button.btn.btn-outline:active {
  background-image: -webkit-linear-gradient(30deg, var(--theme-deafult) 50%, transparent 50%);
  background-image: linear-gradient(30deg, var(--theme-deafult) 50%, transparent 50%);
  color: #ffffff;
  background: var(--theme-deafult);
}

.btn-classic:hover {
  background-color: var(--theme-deafult);
}

.btn-theme {
  background-color: var(--theme-deafult);
  color: #ffffff;
}

/*=====================
    1.2.Title CSS start
    ==========================*/
.title1 {
  text-align: center;
}
.title1 h4, .title1 .h4 {
  color: var(--theme-deafult);
  padding-bottom: 10px;
  margin-bottom: 0;
  margin-top: -3px;
}
.title1 .title-inner1 {
  padding-bottom: 15px;
  margin-bottom: 30px;
  position: relative;
}
.title1 .title-inner1:after {
  position: absolute;
  height: 5px;
  width: 70px;
  background-color: var(--theme-deafult);
  content: "";
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: 0;
}
.title1.title5 .title-inner1 {
  padding-bottom: 0;
  margin-bottom: 0;
}
.title1.title5 .title-inner1:after {
  display: none;
}
.title1.title5 hr[role=tournament6] {
  border: 0px solid;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), var(--theme-deafult), rgba(0, 0, 0, 0));
  display: block;
  width: 25%;
  margin: 15px auto 30px auto;
}
.title1.title5 hr[role=tournament6]::before {
  position: absolute;
  background-color: #efefef;
  border: 1px solid;
  border-color: var(--theme-deafult);
  padding: 5px;
